.header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  position: fixed;
  width: 100%;
  height: 90px;
  z-index: 10;
}


.header-bg {
  background-color: rgba(0, 0, 0, 0.8);
  transition: 0.4s;
}

.nav-menu {
  display: flex;
}

.nav-menu li {
  padding: 0 1rem;
}

/*for targeting Link tag simpley target anchor tag a*/

.nav-menu li a {
  font-size: 1.2rem;
  font-weight: 500;
}

.dropdown {
  display: none;
}

@media screen and /*whenever you are minimizing your screen*/ (max-width: 1040px) {
  .nav-menu {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background: #000;
    position: absolute;
    top: 0;
    left: -100%;
    z-index: -3;
    transition: 0.2s;
  }

  .nav-menu.active {
    left: 0;
  }

  .nav-menu li {
    padding: 1rem 0;
  }

  .nav-menu li a {
    font-size: 2rem;
  }

  .dropdown {
    display: initial;
  }
}
