.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}

.intro-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.7;
}

.hero{
    height: 100%;
    width: 100%;
}

.hero .content{
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}
  

.hero .content h1{
    font-size: 5rem;
    padding: 0.5rem 0 1.5rem 0;
}

.hero .content .h5{
    color: #fff;
    margin-top: -1.5rem;
    margin-bottom: 2rem;
    font-size: 1.4rem;
}


.hero .content p{
    font-size: 1.7rem;
    font-weight: 200;
    text-transform: uppercase;
    margin-top: -1rem;
}

.content .btn{
    margin: 1rem 0.3rem;
}

@media screen and (max-width: 640px){
    .hero .content h1{
        font-size: 3rem;
    }
    .hero .content p{
        font-size: 1.2rem;
        font-weight: 200;
        text-transform: uppercase;
    }
}